/**
 * Events uploader
 *
 * Uploads code digest events from queue to Supabase
 */

import type { SupabaseClient } from "@supabase/supabase-js";
import type { Database } from "@zest/types/database.types";
import { getValidSession } from "../auth/session-manager.js";
import { EVENTS_QUEUE_FILE, PLATFORM, SOURCE } from "../config/constants.js";
import type { ClaudeExtractedEvent, CodeDigestEventInsert } from "../types/index.js";
import { logger } from "../utils/logger.js";
import { clearQueue, readQueue } from "../utils/queue-manager.js";

/**
 * Deduplicate events by ID, keeping the most recent version
 */
function deduplicateEvents(events: ClaudeExtractedEvent[]): ClaudeExtractedEvent[] {
  const eventMap = new Map<string, ClaudeExtractedEvent>();

  for (const event of events) {
    if (!event.id) continue;

    const existing = eventMap.get(event.id);
    if (!existing) {
      eventMap.set(event.id, event);
      continue;
    }

    // Keep the event with the most recent timestamp
    const existingTime = existing.timestamp ? new Date(existing.timestamp).getTime() : 0;
    const currentTime = event.timestamp ? new Date(event.timestamp).getTime() : 0;

    if (currentTime >= existingTime) {
      eventMap.set(event.id, event);
    }
  }

  return Array.from(eventMap.values());
}

/**
 * Upload all queued code digest events to Supabase
 */
export async function uploadEvents(
  supabase: SupabaseClient<Database>,
): Promise<{ success: boolean; uploaded: number }> {
  try {
    const session = await getValidSession();
    if (!session) {
      logger.debug("Not authenticated, skipping events upload");
      return { success: false, uploaded: 0 };
    }

    // Read queued events
    const queuedEvents = await readQueue<ClaudeExtractedEvent>(EVENTS_QUEUE_FILE);

    if (queuedEvents.length === 0) {
      logger.debug("No events to upload");
      return { success: true, uploaded: 0 };
    }

    // Deduplicate events (queue might contain duplicate entries from multiple extractions)
    const uniqueEvents = deduplicateEvents(queuedEvents);

    if (uniqueEvents.length < queuedEvents.length) {
      logger.info(
        `Deduplicated events: ${queuedEvents.length} → ${uniqueEvents.length} (removed ${queuedEvents.length - uniqueEvents.length} duplicates)`,
      );
    }

    logger.info(`Uploading ${uniqueEvents.length} code digest events`);

    // Transform extracted data to full insert data
    const eventsToUpload: CodeDigestEventInsert[] = uniqueEvents.map((e) => ({
      ...e,
      event_type: "file.changed",
      user_id: session.userId,
      platform: PLATFORM,
      source: SOURCE,
    }));

    // Upload events in batches
    const batchSize = 100;
    let uploadedCount = 0;

    for (let i = 0; i < eventsToUpload.length; i += batchSize) {
      const batch = eventsToUpload.slice(i, i + batchSize);

      const { error } = await supabase
        .from("code_digest_events")
        .upsert(batch, { onConflict: "id" });

      if (error) {
        logger.error(`Failed to upload events batch ${i / batchSize + 1}`, error);
        return { success: false, uploaded: uploadedCount };
      }

      uploadedCount += batch.length;
      logger.debug(`✓ Uploaded batch ${i / batchSize + 1} (${batch.length} events)`);
    }

    // Clear queue on success
    await clearQueue(EVENTS_QUEUE_FILE);

    logger.info(`✓ Events upload completed: ${uploadedCount} events`);
    return { success: true, uploaded: uploadedCount };
  } catch (error) {
    logger.error("Failed to upload events", error);
    return { success: false, uploaded: 0 };
  }
}

/**
 * Upload events with retry logic
 */
export async function uploadEventsWithRetry(
  supabase: SupabaseClient<Database>,
  maxRetries = 3,
  backoffMs = 5000,
): Promise<{ success: boolean; uploaded: number }> {
  let lastError: Error | null = null;

  for (let attempt = 1; attempt <= maxRetries; attempt++) {
    try {
      const result = await uploadEvents(supabase);
      if (result.success) {
        return result;
      }

      // If not successful but no exception thrown, don't retry (likely auth issue)
      return result;
    } catch (error) {
      lastError = error as Error;
      logger.warn(`Events upload attempt ${attempt}/${maxRetries} failed: ${lastError.message}`);

      if (attempt < maxRetries) {
        const delay = backoffMs * attempt; // Exponential backoff
        logger.debug(`Retrying in ${delay}ms...`);
        await new Promise((resolve) => setTimeout(resolve, delay));
      }
    }
  }

  logger.error(`Events upload failed after ${maxRetries} attempts`, lastError);
  return { success: false, uploaded: 0 };
}
